<template>
    <div id="register">
      <van-nav-bar
        title=""
        left-arrow
        @click-left="onClickLeft"
      />
      <div class="zc">注册</div>
      <div class="register">
        <div class="help-input">
          <img src="../../static/img/icon_sj@3x.png"/>
          <input type="number" placeholder="请输入手机号" v-model="phone">
        </div>
        <div class="help-input">
          <img src="../../static/img/icon_mm@3x.png"/>
          <input type="password" placeholder="请输入密码" v-model="password">
        </div>
        <div class="help-input">
          <img src="../../static/img/icon_sj@3x.png"/>
          <input type="text" placeholder="请输入邀请码" v-model="invite">
        </div>
        <div class="help-input">
          <img src="../../static/img/icon_yzm@3x.png"/>
          <input type="text" placeholder="请输入验证码" v-model="inputCode">
          <span @click="getCode" :class="{codeStyle:codeName!='获取验证码'}">{{codeName}}</span>
        </div>
      </div>

      <div class="sub" @click="register">注册</div>
    </div>
</template>

<script>
    export default {
      name: "register",
      data(){
        return {
          phone:'',
          password:'',
          code:'',
          invite:'',
          inputCode:'',
          codeName:'获取验证码',
          codeTimes:null,
          codeTime:60,
        }
      },
			created(){
				this.url = this.$route.query.invit
// 				localStorage.setItem("url",this.url)
// 				alert(localStorage.setItem("url"))
				if(this.url!=undefined||this.url!=null||this.url!=''){
					this.invite = this.url
				}else{
					this.invite = ''
				}
			},
      methods: {
        getCode(){
          if(this.codeName!=='获取验证码'){
            return false;
          }
          var reg=11 && /^((13|14|15|17|18)[0-9]{1}\d{8})$/;
          if(this.phone==''){
            this.$toast('请输入手机号');
            return false;
          }else if(!reg.test(this.phone)){
            this.$toast('手机格式不正确');
            return false;
          }
          this.codeName='请等待'+this.codeTime+'S';
          let params = {
            mobile: this.phone
          }
          this.$POST('/auth/hysms',params,false).then(res =>{
            if(res.code===200){
              this.code=res.data;
              this.codeTimes=setInterval(()=>{
                this.codeTime--;
                this.codeName='请等待 '+this.codeTime+' S';
                if(this.codeTime===0){
                  this.codeName='获取验证码';
                  clearInterval(this.codeTimes);
                  this.codeTime = 60;
                }

              },1000);
              console.log(this.code);
            }else{
              this.$toast(res.msg);
            }
          }).catch(res=>{
            this.$toast('获取验证码失败!');
            this.codeName='获取验证码';
            clearInterval(this.codeTimes);
            console.log(res);
          })
        },
        onClickLeft() {
          this.$router.go(-1)
        },
        register(){
					// alert(this.invite)
          var reg=11 && /^((13|14|15|17|18)[0-9]{1}\d{8})$/;
          if(this.phone==''){
            this.$toast('请输入手机号');
            return false;
          }else if(!reg.test(this.phone)){
            this.$toast('手机格式不正确');
            return false;
          }else if(this.password=="") {
            this.$toast('请输入密码');
            return false;
          }else if(this.inputCode=="") {
            this.$toast('请输入验证码');
            return false;
          }
          let params = {
            mobile: this.phone,
            password: this.password,
            invite: this.invite,
            yzm:this.inputCode
            /*code: this.code,*/
          }
          this.$POST('/auth/add',params,true).then(res =>{
            if(res.code===200){
              this.$router.push({path: '/login'});
              this.$toast('注册成功');
            }else{
              this.$toast(res.msg);
            }
          }).catch(res=>{
            console.log(res);
          })
        }
      },
    }
</script>

<style scoped>
 .register{
   width: 80%;
   margin: 0 auto;
 }
  .zc{
    text-align: center;
    font-size: 20px;
    margin-bottom:60px;
    margin-top: 20px;
  }

 .help-input{
   text-align: left;
   border-bottom: 1px solid #bfbfbf;
   padding-bottom: 10px;
   margin-bottom:25px;
 }
 .help-input>img{
   display: inline-block;
   width: 20px;
   position: relative;
   top:8px;
   margin-left: 5px;
 }
 .help-input>input{
   width:60%;
   border: 0;
   font-size: 18px;
   margin-left: 5px;
 }
 .sub{
   width: 80%;
   margin: 0 auto;
   border-radius: 30px;
   background: linear-gradient(to right, #fe5741 , #fd6c46,#fc7f4a);
   height: 45px;
   line-height: 45px;
   color: #ffffff;
   font-size: 18px;
   margin-top: 50px;
 }
 .help-input>span{
   float: right;
   line-height: 50px;
   color: #fe513f;
 }
</style>
<style>
  .van-hairline--bottom::after{
    border-bottom: 0;
  }
  .van-nav-bar .van-icon{
    color: #000;
    font-size: 20px;
  }
</style>



